import { httpGet, httpPost, getUrl } from './http.js';
var loading = null;
function oauth() {
	var auth = sessionStorage.Authorization;
	if (!auth) {
		var url = getUrl("/code.html#" + location.pathname + location.search);
		httpGet('/wechat/portal/oauth/url',
			{ url: url },
			function (response) {
				window.location.replace(response);
			},
			function (xhr, type) {
				var getUrlNumber = sessionStorage.GetOauthUrlNumber;
				if (getUrlNumber) {
					getUrlNumber = parseInt(getUrlNumber) + 1;
				}
				else {
					getUrlNumber = 1;
				}

				if (getUrlNumber > 3) {
					window.stop ? window.stop() : document.execCommand("Stop");
				}
				else {
					sessionStorage.GetOauthUrlNumber = getUrlNumber;
					window.location.reload();
				}
			});
	}
	else {
		// normal
		$('body').removeAttr('style');
		loading.hide(function () {
			console.log('`loading` has been hidden');
		});
	}
	//}
}

var ua = window.navigator.userAgent.toLowerCase();
if (ua.match(/MicroMessenger/i) == 'micromessenger') {
	$('body').attr('style', 'display: none');
	loading = weui.loading('加载中...');
	oauth();
} else {
	console.log('Not Weixin!');
	window.stop ? window.stop() : document.execCommand("Stop");
}


